/*General Content*/

*{
  margin:0;
  padding:0;
  box-sizing:border-box;
  transition: all 0.1s ease-in-out;
  user-select: none;
}

html,body, #content,.App, #root{
  width: 100%;
  height:100%;
}

.App{
  display: flex;
  flex-direction: column;
}

body{
  background-image: url(./img/triangle.png);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

#content{
  display: flex;
  flex-direction: column;
}

.pageHeader{
  display:flex;
  justify-content:center;
  align-items:center;
  width:100%;
  height: fit-content;
  color:white;
  font-size:2em;
  font-weight:bold;
  padding-top:30px;
  padding-bottom:30px;
}

.pageContent{
  padding:10%;
  color:white;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.contentBox{
  background-color: rgba(0, 0, 0, 0.4);
  width: fit-content;
  height: fit-content;
  border-radius: 15px;
  padding: 10px;
}

.shadowed{
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%);
}

.transparent{
  backdrop-filter: blur(5px);
  background-color: rgba(0, 0, 0, 0.4)!important;
  background-image: url('data:image/png;base64,');
}

.shadow {
  border-radius: 1px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 1px 8px rgba(0, 0, 0, 0.2);
}

.cards{
  display:flex;
  flex-wrap:wrap;
  justify-content:center;
  align-items:center;
}


/*Navbar*/

.navBar{
  display:flex;
  justify-content:space-between;
  align-items:center;
  width:100%;
  height: fit-content;
  background-color:#343a40;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 8px;
  padding-bottom: 8px;
  margin-right: 16px;
}

.pageName{
  display: inline-block;
  padding-top: .3125rem;
  padding-bottom: .3125rem;
  margin-right: 1rem;
  font-size: 1.25rem;
  line-height: inherit;
  white-space: nowrap;
  color: white;
  text-decoration: none;
}

.navBarContent{
  display:flex;
  align-items:center;
  width:100%;
  height:100%;
  
}

.navBarLinks{
  display:flex;
  flex-wrap:wrap;
  justify-content:center;
  align-items:center;
}

.navBarLinks a{
  color:rgba(255,255,255,.5);
  text-decoration:none;
  margin-right:10px;
}

.navBarLinks a:hover{
  color:white;
}


/*Scollbar*/
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  background-clip: padding-box;
  border: 4px solid transparent;
}

::-webkit-scrollbar-thumb:hover {
  background-color: rgb(199, 199, 199);
  border: 0;
}

::-webkit-scrollbar-track {
  background-color: #343a40;
}

/*Profile Page*/
.profileContent{
  display: flex;
  flex-direction: column;
  width:100%;
  max-width: 976px;
  margin-left: auto;
  margin-right: auto;
  color:white;
  height: 100%;
}
.profileHeaderContent{
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 24px;
  display: flex;
}

.profilePicture{
  min-width:166px;
  height:166px;
  background-color:white;
  background-image: url(./img/ProfilePic.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.profileText{
  display: flex;
  flex-direction: column;
}

.profileNickname{
  color: white;
  font-size: 1.5em;
  font-weight: bold;
  margin-left: 10px;

}

.profileDescription{
  color: white;
  font-size: 1em;
  margin-left: 10px;
}

.Discord{
  cursor: pointer;
  display: flex;
}

.socialMedia{
  padding-top: 10px;
  margin-left: 10px;
}

.socialMedia a{
  color: white;
  text-decoration: none;
}

/*Popups*/
.infoPopupRight{
  clear: both;
  /* top: 50%;
  left: 50%; */
  transform: translate(0%, -24%);
  width: fit-content;
  height: fit-content;
  background-color: rgba(0, 0, 0, 0.4);
  color:white;
  border-radius: 15px;
  padding: 10px;
  z-index: 10;
  margin-left: 5px;
}

/*Comming soon*/
.commingSoonText{
  color: white;
  font-size: 1.5em;
  font-weight: bold;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.mobileNavButton{
  display: none;
}

@media (min-width: 1180px) {
  .cards{
    padding-left:  5%;
    padding-right: 5%;
  }
}

@media (max-width: 768px) {
  .cards{
    flex-direction:column;
  }
}

@media (max-width: 520px) {
  .mobileNavButton{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 30px;
    height: 30px;
    background-color: rgba(255, 255, 255, 0);
    border:none;
    cursor: pointer;
  }

  .mobileWrapper{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .navBarLinks{
    margin-bottom: 10px;
    	flex-direction: column;
    width: 100%;
    height: 100%;
    }

  .navBarLinks a{
    margin: 0;
    margin-bottom: 7%;
    margin-top: 7%;
    font-size: 26px;
  }

  .mobileNavMenuButtonLine1, .mobileNavMenuButtonLine2, .mobileNavMenuButtonLine3{
    height: 4px;
    background-color: hsl(0, 0%, 100%);
    border-radius: 5px;
    width: 100%;
  }

  .mobileNavButton:hover div{
    background-color: hsl(0, 0%, 75%);
  }

  .navBarContent{
    flex-direction: column;
  }

  .profilePicture{
    min-width:90px;
    height:90px;
  }

  #navBarMobileOpen{
    z-index: 99;
    position: absolute;
    height: 100%;
  }

  #mobileNavButtonOpenState .mobileNavMenuButtonLine1{
    transform: rotate(45deg);
    margin-bottom: -30px;
  }

  #mobileNavButtonOpenState .mobileNavMenuButtonLine2{
    background-color: transparent;
  }

  #mobileNavButtonOpenState .mobileNavMenuButtonLine3{
    transform: rotate(-45deg);
    margin-top: -30px;
  }

}
  



