.projectCard{
    max-width: 400px;
    background-color: #2a2e33;
    border-radius: 5px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.2);
    transition: all 0.3s ease-in-out;
    margin: 7px;
}

.projectCardBody{
    width: 100%;
    height: 100%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.projectCardTitle{
    font-size: 1.2em;
    font-weight: bold;
    color: rgb(255, 255, 255);
    margin: 5px;
}

.projectCardDescription{
    margin: 5px;
    font-size: 11pt;
    color: rgb(255, 255, 255);
}

.projectCardImg{
    margin: 5px;
    height: 200px;
    background-color: #2a2e33;
    border-radius: 5px;
    object-fit: cover;
    object-position: center;
    image-rendering: pixelated;
}

.projectCardLink{
    margin: 5px;
    font-size: 11pt;
    color: rgb(255, 255, 255);
    text-decoration: none;
    font-weight: bold;
    background-color: #2b7cdf;
    max-width: fit-content;
    padding: 5px;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
}

.projectCardLink:hover{
    background-color: #205fad;
}